ding litigation. A good example was in the case of "),_c('em',[_c('strong',[_vm._v("Flamelilly v Zimbabwe Salvage (Pty) Ltd and Another "),_c('sup',[_c('a',{attrs:{"id":"143","href":"#143r"}},[_vm._v("143")])]),_vm._v(" ,")])]),_vm._v(" where the interdict being sought was to stop the respondent from destroying a mining dump until litigation pertaining therefrom had been finalised. R H Christie indicates that the requirements for a final interdict are the same as those for specific performance but are different to those of a final interdict. ")]),_c('p',[_vm._v(" For the aggrieved party to be successful in his/her claim for a prohibitory interdict, they must prove the following: "),_c('ul',[_c('li',[_vm._v(" A clear right – arising from the contract; ")]),_c('li',[_vm._v(" That there is a reasonable apprehension of irreparable harm if the interdict is not granted- The "),_c('em',[_c('strong',[_vm._v("Flamelilly")])]),_vm._v(" case is instructive in this regard since if the respondent had proceeded to destroy the mining, there would be no mining dump at the end. ")]),_c('li',[_vm._v(" That there is no other alternative remedy which would give the applicant the protection sought. The balance of convenience must be in the applicant’s favour, this means that the circumstances must be such that the prejudice suffered by applicant if the interdict was not given would be greater than the prejudice suffered by the respondent if the interdict was granted. The potential prejudice to the applicant should he succeed in the main claim is that he will not be able to get an order for specific performance. ")])])]),_c('p',[_vm._v(" Study the following cases: "),_c('ul',[_c('li',[_c('strong',[_vm._v(" Gideon v Ngumo")]),_vm._v(" 1973 RLR 197 ")]),_c('li',[_c('em',[_c('strong',[_vm._v("Setlogelo v Setlogelo ")])]),_vm._v(" 1914 AD 221 ")])])]),_c('h6',[_c('u',[_c('strong',[_vm._v("Interdict "),_c('em',[_vm._v("pendente lite")])])])]),_c('p',[_vm._v(" TThis ordinarily means obtaining 