dary-heading text-primary"},[_c('strong',[_vm._v("A Declaration Of Rights")])]),_c('p',[_vm._v(" It is worth mention that a declaratory order normally does not form part of the discourse on remedies for breach of contract. The obvious reason being that once the court has rendered a declaration/ruling as to who is entitled to what, whom is obligated to do what… what then? The declaration(s) in the court order will invariably then bring (indirectly so) to the fore the conventional remedies i.e. who must do what (specific performance), who must pay what (damages), who must cease doing what etc. So the question as to whether a declaration of rights is a remedy for breach is an academic one. We will however proceed to have a brief discussion on it below. ")]),_c('p',[_vm._v(" In understanding the concept of declaration of rights, it is apposite that one first draws from the case of "),_c('em',[_c('strong',[_vm._v("Government of the Self-Governing Territory of Kwazulu Natal v Mhlangu "),_c('sup',[_c('a',{attrs:{"id":"148","href":"148"}},[_vm._v("148")])])])]),_vm._v(", where Eloff J pointed out that; ")]),_c('div',[_c('p',[_c('strong',[_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" “...The nature and scope of the right might be inquired into, but in the absence of proof of such a right, or at least a contention that there is such a right, the Court has no jurisdiction. "),_c('u',[_vm._v("This is consistent with the well-established principle that Courts are not there to rule on abstract concepts or on any dispute and, as far as declaratory orders are concerned, only on legally recognisable and enforceable rights.")]),_vm._v("” [My emphasis] ")])])])]),_c('p',[_vm._v(" It is clear that the Court will only declare a right that is legally recognisable and enforceable. To qualify as an “interested person” the applicant must have a real not merely an abstract or intellectual interest in the right or obligation, and the right or obligation must similarly not be 