t to justify cancellation of the contract by the innocent party. If a party attempts to cancel a contract without sufficient justification, the cancellation will be ineffective and the contract will still stand. In addition, the party who tried to cancel will be guilty of repudiation and for this reason, it is very important to determine whether a party has the right to cancel before attempting to exercise this remedy. "),_c('sup',[_c('a',{attrs:{"id":"154","href":"#154r"}},[_vm._v("154")])])])]),_c('li',[_c('u',[_c('strong',[_vm._v(" Ability to restore performance ")])]),_c('p',[_vm._v(" Now since cancellation involves undoing the consequences of a contract, it creates an obligation to restore any performance that has already been received. Therefore, a party seeking cancellation of the contract must be able to return any performance that he received in terms of the contract, in addition to any fruits or proceeds of the performance. Note that this obligation is also subject to the doctrine of accrued rights. ")]),_c('p',[_vm._v(" The inability of the innocent party to restore any performance received would generally prevent her from cancelling the contract. However, the innocent party will still be able to cancel if the inability to restore is not due to the fault of the innocent party and she has not benefited by the performance made: "),_c('em',[_c('strong',[_vm._v("See Feinstein v Niggli "),_c('sup',[_c('a',{attrs:{"id":"155","href":"#155r"}},[_vm._v("155")])])])]),_vm._v(". The following are examples of where an inability to restore performance will not prevent cancellation: "),_c('ul',[_c('li',[_vm._v(" Where the deterioration of the asset is due to an inherent defect, an example being where X delivers a car with an electrical fault to Y. The electrical fault causes an explosion. The car is blown into pieces. ")]),_c('li',[_vm._v(" Perishables. If X delivered tomatoes instead of cherries to Y, then the tomatoes go bad before Y comes to collect them. ")]),_c('li',[_vm._v(" Where the object perished as a result of "),_c('em',[_vm._v("vis major")]),_vm._v(" or a "),_c('em',[_vm._v("casus fortuitous")]),_vm._v(". An example could be where a cow dies from a disease which the innocent party could not prevent or treat. ")]),_c('li',[_vm._v(" Where the object perished when used for the purpose and in the manner in which it was intended. X sells washing powder to Y. When Y uses the washing powder to wash his clothes, it causes all the clothes to turn grey. Y will not be able to return the washing powder. ")]),_c('li',[_vm._v(" Where the contracting party has used/altered the (defective) performance to produce a new product. Although not a settled position, however there is suggestion that it would be equitable to allow cancellation subject to restitution of the new product or providing a monetary equivalent of the original performance. An example is where X purchases 10kg of yeast from Y. X uses the yeast to make beer. However, the yeast proves to be ineffective and the brew fails to turn into beer. ")])])]),_c('p',[_vm._v(" If the innocent party has benefited from a performance, she is unable to return, she must restore the monetary equivalent of the performance. A classical case for this proposition is the case of "),_c('em',[_c('strong',[_vm._v("Sackstein N.O. v Proudfoot SA (Pty) Ltd "),_c('sup',[_c('a',{attrs:{"id":"156","href":"#156r"}},[_vm._v("156")])]),_vm._v(" ,")])]),_vm._v(" where P was employed by the innocent X. X cancelled because of P’s breach and claimed back the salary paid to P. Obviously it was impossible for X to return the services rendered by P. However, the Court held that X had to pay P the monetary equivalent of the services because X had benefitted from P’s performance. ")])]),_c('li',[_c('u',[_c('strong',[_vm._v(" Exercise of the right to cancel ")])]),_c('p',[_vm._v(" The right to cancel is exercised by way of notice of cancellation. Note that there is an important distinction between a notice of rescission and a notice of cancellation. A notice of rescission can be mutually agreed upon by both parties, or it can be done by one ")])]),_c('div',{staticClass:" example z-depth-2 bg2 p-3 m-4"},[_c('h6',{staticClass:"text-left class h6"},[_vm._v("Example 2")]),_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" A party may combine the notice of recession with an advance notice of cancellation "),_c('br'),_c('i',[_vm._v(" “I will have the right to cancel the contract if you do not perform by 1 August of this year (notice of recession), and I elect to cancel the contract if you do not perform in time (notice of cancellation).” ")]),_c('br'),_vm._v(" If the debtor fails to perform by 1 August, the creditor does not have to do anything further. Since he has already communication his election to the debtor, the contract is cancelled automatically on 1st August. ")])]),_c('p',[_vm._v(" party in a contract for a legitimate cause. A notice of cancellation is the actual exercise of the right of cancellation. ")]),_c('li',[_c('u',[_c('strong',[_vm._v(" Cancellation alone ")])]),_c('p',[_vm._v(" Cancelation is a unilateral juristic act performed by the innocent party and it does not require the consent of the guilty party: See "),_c('em',[_c('strong',[_vm._v("Stuart Wrightson (Pty) Ltd v Thorpe "),_c('sup',[_c('a',{attrs:{"id":"157","href":"#157r"}},[_vm._v("157")])]),_vm._v(" .")])]),_vm._v(" A J Kerr "),_c('sup',[_c('a',{attrs:{"id":"158","href":"#158r"}},[_vm._v("158")])]),_vm._v(" asserts the foregoing annotation by stating that “if a party aggrieved by a major breach … wishes to take some active steps to show that the contract is being brought to an end, that is, he wishes to do more than just disregard the contract, he may cancel the contract without asking for any remedies such as restitution or damages”. ")]),_c('p',[_vm._v(" Cancellation can be effected extra-judicially (i.e. without a court order) and does not require confirmation by a Court of Law. In order for an innocent part to exercise his right to cancel the contract: "),_c('ul',[_c('li',[_vm._v(" He must express his intention to cancel the contract by words or by conduct that manifests a clear election to cancel the contract; and ")]),_c('li',[_vm._v(" He must communicate his decision to cancel to the guilty party ")])])])]),_c('li',[_c('strong',[_vm._v(" What does the requirements of communication to the guilty party entail? ")]),_c('p',[_c('ul',[_c('li',[_vm._v(" Some cases have held that the guilty party must have actual knowledge of the innocent party’s intention to cancel. However, actual knowledge does not have to be proved if the innocent party can prove that he took reasonable steps to notify the other party of his election to cancel. See the case of "),_c('em',[_c('strong',[_vm._v("Stuart Wrightson (Pty) Ltd v Thorpe")]),_vm._v(" supra")]),_vm._v(". ")]),_c('li',[_vm._v(" There are no prescribed formalities for cancellation. Provided that there is an unambiguous election to cancel, notification of cancellation can be expressed (in words), implied (by conduct) or may even be contained in a summon "),_c('sup',[_c('a',{attrs:{"id":"159","href":"#159r"}},[_vm._v("159")])]),_vm._v(" The parties can even agree to dispense with the requirement of notice of cancellation. ")]),_c('li',[_vm._v(" The decision to cancel must be conveyed to the guilty party. The only issue is whether the notice of cancellation reached the guilty party: See in this regard "),_c('strong',[_c('em',[_vm._v("Datacolour International (Pty) Ltd v Intamarket (Pty) Ltd "),_c('sup',[_c('a',{attrs:{"id":"160","href":"#160"}}),_vm._v(" 160")])])])]),_c('li',[_vm._v(" In cases of major breach the innocent party does not have to state the grounds for cancellation. Furthermore, if the innocent party states an incorrect or inadequate ground for cancellation he will not be bound thereby. ")])])])]),_c('li',[_c('strong',[_c('u',[_vm._v(" Election ")])]),_c('p',[_vm._v(" Note the following principles of election: "),_c('ul',[_c('li',[_vm._v(" When the terms of payment are fixed in the following manner: [payment is to be by instalments, seller has option to cancel on failure by the purchaser to pay an instalment on due date] - the seller’s right to cancel accrues by reason of the buyers default on the date of payment, and the seller must then elect whether or not he will avail himself of it "),_c('sup',[_vm._v("161")]),_vm._v("; ")]),_c('li',[_vm._v(" If party A commits a breach which, in the language often used, “goes to the root/substance of the contract”, party B may elect to hold him to the contract or to cancel it "),_c('sup',{attrs:{"id":"162"}},[_c('a',{attrs:{"href":"#162r"}},[_vm._v("162")])]),_vm._v("; ")]),_c('li',[_vm._v(" When an event occurs which entitles one party to refuse to carry out his part of the contract that party has a choice; he can either elect to take advantage of the event or he can elect not to do so. He is entitled to a reasonable time in which to make up his mind. "),_c('sup',{attrs:{"id":"163"}},[_c('a',{attrs:{"href":"#163r"}},[_vm._v("163")])]),_vm._v("; "),_c('p',[_vm._v(" It is important to be aware that the election can be done expressly or by conduct. For instance, if the innocent party conducts himself in a way that clearly indicates that he intends to uphold the contract, that fact will be implicit from his conduct. Mere delay in exercising the election to cancel does not automatically mean that the right to cancel is lost. However, if the delay causes the other party to reasonably believe that the innocent party elected to uphold the contract, the right to cancel will be lost. ")]),_c('p',[_vm._v(" A case in point would be that of "),_c('em',[_c('strong',[_vm._v("Mahabeer v Sharma "),_c('sup',{attrs:{"id":"164"}},[_c('a',{attrs:{"href":"#164r"}},[_vm._v("164")])])])]),_vm._v(" wherein a contract of sale was concluded between M and S. M was to pay the purchase price in instalments. These payments were not made. S wrote to M and stated that contracted would be cancelled if payment was not made within 30 days (notice of rescission). M did not pay. 5 months after this letter was sent, S sold the property to someone else. At this time, M’s attorney was informed of the cancellation (notice of cancellation) and subsequent sale. M argued that the right to cancel lapses unless it is exercised within a reasonable time. ")]),_c('p',[_vm._v(" The court held that a mere delay in final notification cannot per se result in the loss of the right to cancel. The delay could however indicate an election to uphold the contract. On the facts, the Court held that an inference could be made, as the guilty party never believed that the innocent party had given up the right to cancel the contract. As a result, S’s right to cancel was not lost. See also "),_c('em',[_c('strong',[_vm._v("Paradyskloof Golf Estate v Stellenbosch Municipality "),_c('sup',{attrs:{"id":"165"}},[_c('a',{attrs:{"href":"#165r"}},[_vm._v("165")])])])]),_vm._v(". ")]),_c('div',{staticClass:"note note-danger"},[_c('i',{staticClass:"fas fa-sticky-note"},[_vm._v("NB:")]),_c('p',{staticClass:"text-left"},[_vm._v(" When the innocent party has elected to cancel, the contract is at an end and there is no possibility to uphold it later on. ")])])])])])]),_c('li',[_c('strong',[_c('u',[_vm._v(" The effect of cancelation ")])]),_c('p',[_vm._v(" Cancelation extinguishes all obligations that need to be performed after the date of cancelation, and further creates an obligation to ")]),_c('div',{staticClass:"example z-depth-2 bg2 p-3 m-4"},[_c('h6',{staticClass:"text-left class h6"},[_vm._v("Example 1")]),_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" A sells his bull to B for E10, 000.00 to mate with B’s cows. The parties agree that A will deliver the bull to B on payment of a E5, 000.00 deposit. They agree that B will pay the remainder of the purchase price in monthly instalments of E1, 000.00 over the next 5 months. B pays the E5, 000.00 deposit and A delivers the bull. However the bull turns out to be impotent. A therefore commits a breach of positive malperformance. B then elects to cancel the contract. The effect of the cancelation is the following: "),_c('ul',[_c('li',[_vm._v(" It creates a duty to restore all performances already made (restitution). B is therefore obliged to return the bull to A and A is obliged to return the E5, 000.00 to B; ")]),_c('li',[_vm._v(" All future obligations are terminated. B will not have to pay the future monthly instalments. ")])])])]),_c('p',[_vm._v(" restore any performance that has already been made. ")])]),_c('li',[_c('strong',[_c('u',[_vm._v(" The Doctrine of accurued rights ")])]),_c('p',[_vm._v(" This doctrine applies where the performances in terms of the contract are divisible or where the contract creates continuous (ongoing) obligations (e.g. rental contracts, employment and service contracts). According to this doctrine, rights that are due and enforceable before cancelation and are independent of any executory part of the contract are not extinguished by cancelation. By the executory part of the contract we mean the obligations which still have to be performed after the date of cancelation. Examples of these are "),_c('sup',[_c('a',{attrs:{"id":"166","href":"#166r"}},[_vm._v("166")])]),_vm._v(". You can also see in this regard the case of "),_c('em',[_c('strong',[_vm._v("Crest Enterprises (Pty) Ltd v Rycklof Beleggings (Edms) Bpk "),_c('sup',[_c('a',{attrs:{"id":"167","href":"#167r"}},[_vm._v("167")])])])]),_vm._v(". ")]),_c('p',[_vm._v(" In other words, where the doctrine of accrued rights applies, cancelation only terminates those obligations that have not yet accrued by the time of cancelation. In effect, we are dealing with partial cancelation – "),_c('ul',[_c('li',[_vm._v(" rights that have not accrued are cancelled; and ")]),_c('li',[_vm._v(" rights that have accrued are enforceable by way of specific performance as if the contract has not been cancelled ")])])]),_c('p',[_vm._v(" There are three requirements for the operation of the doctrine of accrued rights which are as follows: "),_c('ul',[_c('li',[_vm._v(" The contract must be divisible into separate sets of reciprocal performances; ")]),_c('li',[_vm._v(" The accrued rights must be due and enforceable before the date of cancelation; ")]),_c('li',[_vm._v(" The accrued rights must be independent from any outstanding (unperformed) obligation at the time of cancelation. ")])])]),_c('p',[_vm._v(" For further reading on the above requirement students are referred to the following authorities which are instructive on the above principle: "),_c('ul',[_c('li',[_c('em',[_c('strong',[_vm._v("Nash v Golden Dumps (Pyt) Ltd")])]),_vm._v(" 1985 (3) SA 1 (A); ")]),_c('li',[_c('em',[_c('strong',[_vm._v("PBL Management (Pty) Ltd v Telkom SA Ltd")])]),_vm._v(" 2001 (2) SA 313 (T); ")]),_c('li',[_vm._v(" Read also "),_c('em',[_vm._v("“The Principles of the Law of Contract”")]),_vm._v(" by A J Kerr (6th Edition) at pages 733 to 735 on Cancelation and Restitution. ")])])])])])])]),_c('hr',{attrs:{"id":"damages"}}),_c('br'),_c('div',{staticClass:"Chapter"},[_c('ol',{attrs:{"type":"1"}},[_c('h1',{staticClass:"text-center chapter-topic  secondary-heading text-primary"},[_c('strong',[_vm._v("Damages")])]),_c('p',[_vm._v(" Damages may be rewarded to an aggrieved party who shows that he has suffered loss; that the breach of contract is the significant factor or cause in bringing about the loss; that the amount claimed and proved is either as agreed upon or as provided for in the residual rules; and that in appropriate cases the rules or notices relating to cancelation have been complied with. "),_c('sup',[_c('a',{attrs:{"id":"168","href":"#168r"}},[_vm._v("168")])])]),_c('ol',{attrs:{"type":"i"}},[_c('li',[_c('strong',[_c('u',[_vm._v(" The nature of damages for breach ")])]),_c('p',[_vm._v(" Unlike damages for delict, damages for breach of contract are not intended to recompense the innocent party for his loss, but to put him in the position he would have been if the contract had been properly performed. The difference herein was succinctly stated by Van den Heever JA in "),_c('em',[_c('strong',[_vm._v("Trotman v Edwick "),_c('sup',[_c('a',{attrs:{"id":"169","href":"#169r"}},[_vm._v("169")])])])]),_vm._v(": ")]),_c('div',{staticClass:"example z-depth-2  p-3 m-2"},[_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" “A litigant who sues on contract sues to have his bargain or its equivalent in money or in money and kind. The litigant who sues on delict sues to recover the loss which he has sustained because of the wrongful conduct of another, in other words, that the amount by which his patrimony has been diminished by such conduct should be restored to him.” ")])]),_c('p',[_vm._v(" It is worth noting that damages are usually claimed together with further/alternative relief. For example: "),_c('ul',[_c('li',[_vm._v(" One can claim specific performance and damages; ")]),_c('li',[_vm._v(" One can claim specific performance and in the same action ask the Court that, in the event of the defendant’s non- compliance with this order within the stipulated time, the contract be cancelled and damages be rewarded (double- barrelled procedure) ")]),_c('li',[_vm._v(" One can claim specific performance and, in the alternative, damages, that is if the Court exercises is discretion and refuses to grant an order of specific performance, and one wants damages for loss suffered ")]),_c('li',[_vm._v(" One can claim cancellation, restitution and damages ")])])])]),_c('li',[_c('strong',[_c('u',[_vm._v(" Requirements for a claim for damages ")])]),_c('p',[_vm._v(" The plaintiff must prove the following: "),_c('ul',[_c('li',[_vm._v(" Breach of contract by the other party. ")]),_c('li',[_vm._v(" Patrimonial loss ")]),_c('li',[_vm._v(" A causal connection between the breach and the loss (factual causation). ")]),_c('li',[_vm._v(" That the loss is not too remote (legal causation – general versus special damages). The onus is on the party claiming damages to prove these requirements. Once these elements have been proven, the onus shifts to the defendant to prove that there was. ")]),_c('li',[_vm._v(" A duty on the plaintiff to mitigate his losses, which he failed to do. ")])])]),_c('p',[_vm._v(" Study the case of "),_c('strong',[_c('em',[_vm._v("Victor Products SA (Pty) Ltd v Lateulere Manufacturing (Pty) Ltd "),_c('sup',[_c('a',{attrs:{"id":"170","href":"#170r"}},[_vm._v("170")])])])])]),_c('p',[_vm._v(" Let us now consider each of the above requirements in detail: "),_c('ol',{attrs:{"type":"a"}},[_c('li',[_c('strong',[_c('u',[_vm._v(" Breach ")])]),_c('p',[_vm._v(" On this requirement do consider the discussion on breach discussed in the previous chapter. ")])]),_c('li',[_c('strong',[_c('u',[_vm._v(" Patrimonial loss ")])]),_c('p',[_vm._v(" The law adopts a materialistic approach to loss in the context of contract. Loss must affect your patrimony, that is, the breach must cause financial loss. In the case of "),_c('em',[_c('strong',[_vm._v("Administrator, Natal v Edouard "),_c('sup',[_c('a',{attrs:{"id":"171","href":"#171r"}},[_vm._v("171")])])])]),_vm._v(", it was stated that one cannot claim for non-patrimonial loss such as pain and suffering, mental anguish, sentimental losses, physical inconvenience, etc in the law of contract. ")]),_c('p',[_vm._v(" The "),_c('u',[_vm._v("once and for all rule")]),_vm._v(" is applicable: a plaintiff must claim all his damages in one action. This means that he must claim not only for present losses but also for prospective (future) losses. It is however quite difficult to calculate loss, especially in relation to opportunities that the innocent party has lost as a result of the breach. See in this regard "),_c('em',[_c('strong',[_vm._v("SDR Investment Holdings Co. (Pty) Ltd v Nedcor Bank Ltd"),_c('sup',[_c('a',{attrs:{"id":"172","href":"#172r"}},[_vm._v("172")])])])]),_vm._v(". The courts therefore do not insist that the plaintiff quantify his loss with mathematical procession. The plaintiff must however, produce the best evidence that he can prove the quantum of his loss. ")]),_c('p',[_vm._v(" A breach of contract can have both detrimental and beneficial consequences. To the extent that both flow from the breach and taking account of the question of remoteness, it is often said that both should in principle be taken into account to determine the net effect on the plaintiff’s financial position. The following example is made: ")]),_c('div',{staticClass:"example z-depth-2"},[_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" If, in a contract for the sale of a house, the purchaser is given the right of occupation of the house and the seller fails to evict the tenants, the seller commits a breach of contract. The tenants, whilst in occupation of the house, must however, pay rent to the purchaser. The rental income is a beneficial consequence that must be taken into account in accessing the purchaser’s damages ")])])]),_c('li',[_c('strong',[_c('u',[_vm._v(" Factual Causation ")])]),_c('p',[_vm._v(" Factual causation is a question of fact-we use the "),_c('em',[_vm._v("conditio sine qua non")]),_vm._v(" test (the but for test) to ask whether this loss would have occurred but for the breach of the contract. If the damage would not have occurred, the breach caused the damage. It has been stated by previous authorities the uncertainty whether the innocent party’s claim could fail or be reduced if he caused his own losses due to contributory negligence. However, this question has been settled by the Supreme Court of South Africa in the case of "),_c('strong',[_c('em',[_vm._v("Thoroughbred Breeders Association v Price Waterhouse"),_c('sup',[_c('a',{attrs:{"id":"173","href":"#173r"}},[_vm._v("173")])])])]),_c('strong',[_vm._v(" [Study the judgment]")])])]),_c('li',[_c('strong',[_c('u',[_vm._v(" Remoteness of Harm (legal Causation) ")])]),_c('p',[_vm._v(" It is in the interest of fairness to the defendant, that a line be drawn between damages caused by his breach for which he is to be held liable and damages which, although in the broad sense of the word caused by his breach, are so remote from it that he ought not to be held liable for them. The starting point is the judgment of Corbbett JA in "),_c('strong',[_c('em',[_vm._v("Holmdene Brick Works (Pty) Ltd v Roberts Construction Co. Ltd "),_c('sup',[_c('a',{attrs:{"id":"174","href":"#174r"}},[_vm._v("174")])])])]),_vm._v(". ")]),_c('p',[_vm._v(" Basically, legal causation entails that the harm must be reasonable foreseeable at the time of entering into contract. The English case of "),_c('strong',[_c('em',[_vm._v("Hadley v Baxendale "),_c('sup',[_c('a',{attrs:{"id":"175","href":"#175r"}},[_vm._v("175")])])])]),_vm._v(", stated as follows: ")]),_c('div',{staticClass:"example z-depth-2 p-3 m-2"},[_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" “Where two parties have made a contract which one of them has broken, the damages which the other party ought to receive in respect of such breach of contract should be such as may fairly and reasonable be considered either arising naturally, i.e. according to the usual course of things, from such breach of contract itself, or such as may reasonable be supposed to have been in the contemplation of both parties, at the time they made the contract, as the probable result of the breach of it.” ")])]),_c('p',[_vm._v(" Instead of using a straight forward test based on foreseeability to determine legal causation, the Court complicate the issue by drawing a distinction between general damages (intrinsic loss) and special damages (extrinsic loss). "),_c('ol',[_c('li',[_vm._v(" General Damages (intrinsic Loss): "),_c('p',[_vm._v(" This is the normal sort of earm that you would expect to flow from such a breach of contract – the natural and probable consequences of the breach. The general damages, as thus defined, cause little difficulty. They do not have to be specially pleaded, and the question in each case is simply whether they flow naturally and generally from the kind of breach that has been committed."),_c('sup',[_c('a',{attrs:{"id":"176","href":"#176r"}},[_vm._v("176")])]),_vm._v(" For example, it is normal and probable that the failure to sterilise a woman would result in her having further children which she would have to support. The costs of support these children is general damages. ")])]),_c('li',[_vm._v(" Special Damages (Extrinsic Loss): "),_c('p',[_vm._v(" Although the loss is factually caused by the breach, it is unusual because of some special circumstances or interest of the innocent party. The guilty party would not in the ordinary course of events expect such loss in the case of breach. An example could be, a woman contracts with a doctor to perform a sterilisation because she suffers from High Blood Pressure, a medical condition that is aggravated by pregnancy. As a result of the unplanned pregnancy her blood pressure becomes so high that she suffers a stroke which paralyses her so that she will never be able to work again. The woman’s loss of income as a result of the stroke is special damage-not every woman who becomes pregnant after a failed sterilisation will suffer a stroke. ")])]),_c('p',[_vm._v(" With special damages there lies a problem in their recovery in that they are generally not foreseeable. However, the Courts have held that if the plaintiff can show on the facts that the loss was reasonable foreseeable, the damages can be calculated. ")]),_c('p',[_vm._v(" For further reading on the subject refer to the following authorities: "),_c('ul',[_c('li',[_c('strong',[_c('em',[_vm._v("Lavery & Co. Ltd v Jungheinrich")])]),_vm._v(" 1931 AD 156; ")]),_c('li',[_c('strong',[_c('em',[_vm._v("Sharz Investment v kalovyrnas")])]),_vm._v(" 1976 (2) SA 545 (A); ")]),_c('li',[_c('strong',[_c('em',[_vm._v("Thoroughbred Breeders Association v Price Waterhouse")])]),_vm._v(" 2001 (4) SA 551 (SCA) 579-583; ")]),_c('li',[_c('strong',[_c('em',[_vm._v("Transnet Ltd t/a National Ports Authority v Owner of MV Snow Crystal")])]),_vm._v("2008 (4) SA 111 (SCA) paragraphs 31-36 ")])])])])])])])])]),_c('li',[_c('strong',[_c('u',[_vm._v(" Mitigation of Damages ")])]),_c('p',[_vm._v(" When a contract has been breached the innocent party is not entitled to sit back and allow damages to multiply. It is his duty to mitigate his damages. The duty was well expressed by Stratford CJ in "),_c('em',[_c('strong',[_vm._v("Hazis v Transvaal and Delagoa Bay Investments Co. Ltd "),_c('sup',[_c('a',{attrs:{"id":"177","href":"#177r"}},[_vm._v("177")])])])]),_vm._v(" as follows: ")]),_c('div',{staticClass:"example z-depth-2 bg2 p-3 m-4"},[_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" “This rule about mitigating damages relates not to what the claimant in fact did, but to what he should have done. It is in essence a claim based on negligence-neglect to do what a reasonable man would do if placed n the position of the person claiming damages. The defendant in such claim says ‘admitting that in fact you suffered those damages, you have only yourself to blame for having suffered so much, or at all because you did not take reasonable steps to protect yourself and, therefore, me’.” ")])]),_c('p',[_vm._v(" It is further to be noted that in mitigation of damages the plaintiff does not have to plead and prove that he has done what is reasonable to mitigate his damages, because the onus is on the defendant to prove that he has not. The plaintiff is not required to do more than a reasonable man would do to mitigate his damages, and the defendant’s onus of proving that the plaintiff has not acted reasonable is difficult to charge. In "),_c('strong',[_c('em',[_vm._v("Holmdene Brick Works (Pty) Ltd v Roberts Construction")])]),_vm._v(" supra, Corbett JA said: ")]),_c('div',{staticClass:"example z-depth-2 bg2 p-3 m-4"},[_c('blockquote',{staticClass:"blockquote text-left"},[_vm._v(" “Being a question of mitigation, the onus of establishing that there were other less costly remedies which the respondent ought to have adopted rested upon the appellant. In a case such as the present one, where the breach of contract creates something of an emergency and the sufferer finds himself in a position of embarrassment as a consequence of the breach, the measures which he may be forced to adopt to extricate himself ought not to be weighed in nice scales and the Court should not be astute to hold that this onus has been discharged. The law is satisfied if the sufferer from the breach has acted reasonable in the adoption of remedial measures.” ")])])])])])]),_c('hr',{attrs:{"id":"unitSammary"}}),_c('br'),_c('div',{staticClass:"Chapter"},[_c('h1',{staticClass:"text-center chapter-topic secondary-heading text-primary"},[_c('strong',[_c('i',{staticClass:"fas fa-book-reader"}),_vm._v(" Summary")])]),_c('p',{staticClass:"text-left"},[_vm._v(" In summary, it is evident that there are five (5) remedies available for breach or threatened breach of contract. These are generally, specific performance, interdict, declaration of rights, cancellation and damages. The first three (3) may be regarded as methods of enforcements and the last two methods of claiming compensation for non- performance. As was demonstrated in our discussion on these remedies, it was clear that the choice between these remedies rests primarily with the injured party, the plaintiff, who may choose more than one of them, either in the alternative or together, subject to the general principles that he may not claim inconsistent remedies and he may not be over compensated. Can a party to a contract transfer his or her rights and obligations to another person? We shall discuss this in the next unit. Enjoy! ")])]),_c('hr',{attrs:{"id":"discussion"}}),_c('br'),_c('div',{staticClass:"Chapter"},[_c('h1',{staticClass:"text-center chapter-topic secondary-he