s and warranties in contracts. ")]),_c('br'),_vm._m(2),_vm._m(3),_vm._m(4),_c('br'),_vm._m(5),_c('br'),_vm._m(6),_c('br'),_vm._m(7),_c('br'),_c('div',{staticClass:"text-left"},[_c('h4',{staticClass:"h4"},[_c('mdb-icon',{attrs:{"icon":"book-open"}}),_vm._v(" Study Skills")],1),_c('strong',[_vm._v("The study skills that will be required in this Unit are as follows:")]),_vm._m(8)]),_c('br'),_vm._m(9),_c('br'),_vm._m(10)])]),_c('hr',{attrs:{"id":"facts"}}),_c('br'),_vm._m(11),_c('hr',{attrs:{"id":"issues"}}),_c('br'),_vm._m(12),_c('hr',{attrs:{"id":"unitSammary"}}),_c('br'),_vm._m(13),_c('hr'),_c('br'),_vm._m(14),_c('hr',{attrs:{"id":"activity2"}}),_c('br'),_vm._m(15),_c('hr',{attrs:{"id":"selfAssessment"}}),_c('br'),_vm._m(16)])])],1)}
