 Reading")]),_c('ul',[_c('li',[_vm._v("Robert Sharrock “Business Transactions Law” 9th Ed, Juta")]),_c('li',[_vm._v("Dale Hutchison “ The Law of Contract in South Africa”, 3rd Ed, Oxford")]),_c('li',[_vm._v("Gibson, “South African Mercantile and Company Law” 8th Ed, Juta")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"Chapter"},[_c('h1',{staticClass:"text-center chapter-topic secondary-heading text-primary"},[_c('strong',[_vm._v("Facts")])]),_c('p',[_vm._v(" You will recall that one of the units in LAW203, we defined terms and the classifications according to modern and Roman-Dutch Law. To avoid confusion, our main focus was on terms and we did not touch on conditions. It is very important to mention at the very inception of this unit that there is big difference between terms, conditions and warranties. Let us distinguish terms from conditions. ‘Terms’ in a contract serve to determine the contents of a contract. On the other hand, a condition is a term which makes the enforceability or consequence of the contract dependent on the occurrence or non-occurrence of an uncertain future event.1 ")]),_c('p',[_vm._v(" A condition is a major term of the contract which goes to the root of the contract. If a condition is breached the innocent party is entitled to repudiate (end) the contract and claim damages2. This principle was clearly illustrated in the old case of Poussard v. Spiers3. ")]),_c('p',[_vm._v(" The Claimant (Poussard) was an opera singer. She was contracted by the defendant to perform in that capacity for a duration of three months. This was to subject to certain conditions, such as a salary of £11 per week, a start of “on or about” the 14th of November and an option to re-engage the Claimant’s services for another three months for a salary not exceeding 14 pounds per week. Instead of the 14th of November however, the launch performance was subsequently scheduled for the 28th of November, to which the Claimant gave no objection. However, she fell ill just before the start of the opera and could not sing for the first three days. The defendant hired another singer as potential cover and then actual cover when the claimant could not sing for the first three days of performances. Once the Claimant was well again, she wanted to take up her position in the performance but this was refused by the Defendant. An action for wrongful dismissal was then launched against the defendant. At trial, the jury found in favour of the defendant and awarded them the right to claim £83 from the Claimant, as it had been reasonable to hire her replacement. The Claimant appealed against this. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"Chapter"},[_c('h1',{staticClass:"text-center chapter-topic secondary-heading text-primary"},[_c('strong',[_c('i',{staticClass:"fas fa-puzzle-piece"}),_vm._v(" Issues")])]),_c('ol',[_c('p',[_vm._v(" The issue in this case was whether failing to turn up to the first day of performance amounted to a breach of a condition of the contract. ")]),_c('hr',{attrs:{"id":"held"}}),_c('br'),_c('h1',{staticClass:"text-center chapter-topic secondary-heading text-primary"},[_c('strong',[_vm._v("Held")])]),_c('p',[_vm._v(" It was held that failure to turn up did amount to a breach of a condition of the contract as this went to its very root and that Spiers were therefore free to rescind the contract. ")]),_c('div',{staticClass:"example z-depth-2 bg2 p-3 m-4"},[_c('h6',{staticClass:"text-left class h6"},[_vm._v("Example 1")]),_c('em',[_c('p',[_vm._v(" Nomcebo promises Phindile E500, 00 if she travels to Germany in May. The contract is concluded in January, but Phindile may claim the money only if she fulfils the condition. What is the condition? The operation of the contract is suspended and rendered dependent on an uncertain future event. If on the other hand, Nomcebo promises Phindile E500, 00 in February with the stipulation that she must travel to Germany in May, we are not dealing with a condition. Phindile may claim the E500, 00 immediately, but Nomcebo will be able to sue her for breach of contract if she does not fulfil her obligation. The operation of the contract is not rendered dependent on an uncertain future event in the second scenario: the agreement is unconditional and immediately enforceable. The intention of the parties determines whether we are dealing with a condition or term (in the form of a counter- ")])])]),_c('hr',{attrs:{"id":"typeOfCondition"}}),_c('br'),_c('div',[_c('h1',{staticClass:"text-center  secondary-heading text-primary chapter-topics"},[_c('strong',[_vm._v("Types Of Conditions ")])]),_c('p',[_vm._v(" In a unanimous judgment, De Villiers A.J. sitting with Watermeyer J in R. v Katz4 explaining the types of conditions, it was held as follows: ")]),_c('p',[_vm._v(" “The word ‘condition’ in relation to a contract is sometimes used in a wide sense as meaning a provision of the contract, i.e. an accepted stipulation, as for example in the phrase ‘conditions of sale’. In this sense the word includes ordinary arrangements as to time and manner of delivery and of payment of the purchase price, etc-in other words the so-called accidentalia of the contract. In the sense of a true suspensive or resolutive condition, however, the word has a much more limited meaning, viz. of a qualification which renders the operation and consequences of the whole contract dependent upon an uncertain future event…. Where the qualification defers the operation of the contract, the condition is suspensive, and where it provides for dissolution of the contract after interim operation, the condition is resolutive. The exact dividing line between the two classes is sometimes difficult to draw, because failure of a suspensive condition may have a resolutive effect, and a resolutive condition in a sense suspends dissolution of the contract. But for present purposes that aspect of the matter need not be pursued. What is of importance is the distinction between true conditions of either kind and ordinary stipulations falling outside their category. In the case of true conditions the parties by specific agreement introduce contingency as to the existence or otherwise of the contract, whereas provisions which are not true conditions bind the parties as to their fulfilment and on breach give rise to ordinary contractual remedies of a compensatory nature, i.e. (depending on the circumstances) specific performance, damages, cancellation or certain combinations of these…” ")]),_c('p',[_vm._v(" The above -mentioned case was very instrumental to the judgment of the Swaziland High Court in the case of Norlaw Investments (Pty) Ltd v Eric Slabbert Agencies (Pty) Ltd5. ")]),_c('p',[_vm._v(" From the summary above, I am sure it has dawned to the reader that in the strict sense there are two types of conditions. These are the suspensive conditions and resolutive conditions. We will briefly unpack them below. "),_c('ol',{attrs:{"type":"a"}},[_c('li',[_c('h5',{staticClass:"h5"},[_vm._v(" Suspensive Condition")]),_c('ol',[_c('li',{staticClass:"h6"},[_vm._v("Nature")]),_c('p',[_vm._v(" According to Sharrock6 by its very nature, the suspensive condition suspends the operation of an obligation arising from the contract (or a number of obligations, or the whole contract) until the occurrence or non-occurrence as the case may be, of future uncertain event. This definition was also upheld in the case of Tamarillo (Pty) Ltd v. B N Aiken (Pty) Ltd7. Other jurists have suggested from time to time that where there is a suspensive condition there is no contract at all until the condition has been fulfilled.8 Note that this view is due to a misconception that the contract will not be binding unless a certain condition is fulfilled. But it is important to note the literal meaning of the word ‘suspensive’- an operative principle denoting the suspension of an obligation, not the entire agreement. Gibson9 and Sharrock10 therefore argue that a contract that is subject to a suspensive condition is a valid and binding contract from the moment of its conclusion. This basically means that obligations are created when the contract is completed and neither party can resale from it. It is a particular obligation that cannot be enforced until the condition is fulfilled. ")]),_c('li',{staticClass:"h6"},[_vm._v("The Position of the Parties prior to fulfillment")]),_c('p',[_vm._v(" According to Sharrock11, prior to the fulfillment of the condition (occurrence or non-occurrence of the specified event), the conditional debtor is not liable to perform the suspended obligation. But the parties are contractually bound to each other and each must carry out any obligations which are not affected by the condition. See the example below as an illustration of this principle: ")]),_c('div',{staticClass:"example z-depth-2 bg2 p-3 m-4"},[_c('h6',{staticClass:"text-left class h6"},[_vm._v("Example")]),_c('em',[_c('p',[_vm._v(" B buys S’s house on condition that he (B) is able to raise a loan in a specified sum or sell his own house on certain conditions. It is agreed that B will take immediate occupation of the property and pay a certain amount each month as “occupational rental”. Prior to his obtaining the loan or selling his house, B is not required to pay the purchase price to S; but he can insist upon S giving occupation of the property, and can be made to pay the occupational rental. ")])])]),_c('li',{staticClass:"h6"},[_vm._v("The Effect of Fulfillment or Failure")]),_c('p',[_vm._v(" As it has been alluded to above that a conditional agreement remains valid despite the inclusion of a suspensive condition. Similarly, where a suspensive condition is fulfilled, the debtor must perform the obligation which was suspended12. Where a suspensive condition fails, the suspended obligation comes to an end (unless the conditional debtor has waived fulfillment). In the case of Tuckers Land and Development Corporation (Pty) Ltd v. Strydom, it was stated that a sale subject to suspensive conditions is a sale, although consequences such as the passing of risk do not take place until the sale is perfected by the fulfilment of the condition. Another landmark case which clearly illustrate the principle in discussion is that of Africast (Pty) Ltd v. Pangbourne Properties Ltd13 [You are required to study this judgment]. ")]),_c('p',[_vm._v(" It is also worth noting that obligations which were not affected by the condition also terminate if the agreement so provides, or, if they were agreed to in contemplation of the condition being fulfilled, for example, an obligation to give immediate occupation or delivery in terms of a sale which is conditional upon the buyer obtaining a loan. ")]),_c('li',{staticClass:"h6"},[_vm._v("The time of Fulfilment")]),_c('p',[_vm._v(" According to Sharrock, if no time for the occurrence (or non-occurrence) of the event has been agreed upon, expressly or impliedly, the law holds that the condition fails if the event does not take place within a reasonable time. Important factors to be borne in mind in determining whether a reasonable time has elapsed are: difficulties, obstacles and delays which the parties contemplated, or ought to have foreseen, at the time of entering into the contract, and the commercial interests of each of the parties14. A case in point is that of Cardoso v. Tuckers Land and Development Corporation (Pty) Ltd15[You are required to study the judgment]. ")]),_c('li',{staticClass:"h6"},[_vm._v("Fictional Fulfilment")]),_c('p',[_vm._v(" The doctrine of fictional fulfilment provides that if a party to a contract which is subject to a suspensive condition deliberately prevents fulfilment of the condition to avoid being bound by the contract, the condition may be deemed to have been fulfilled. MacDuff & Co Ltd (in Liquidation) v Johannesburg Consolidated Investment Co Ltd 16 is the leading case in South African contract law on the issue of fictional fulfilment of suspensive conditions. The case involved two companies: Johannesburg Consolidated Investment (JCI) which was a minority shareholder in Macduff & Co. JCI had entered into a contract with Macduff whereby the latter would undergo voluntary liquidation. Macduff agreed that, once liquidated, it would transfer all its assets into a newly founded company under the auspices and supervision of JCI. In the agreement was a suspensive condition which permitted liquidation only if the shareholders of Macduff agreed to it. ")]),_c('p',[_vm._v(" At the time of contracting, this was a promising deal for JCI. When market conditions changed, however, it was no longer of much economic benefit. JCI accordingly bought up shares in Macduff, until it was the majority shareholder and then voted against liquidation, deliberately obstructing the possibility of the occurrence of the suspensive condition in the contract. The Appellate Division ruled that such obstruction was unlawful, and therefore that there was a fictional fulfilment of the suspensive condition. JCI was thus held liable for breach of contract. ")]),_c('p',[_vm._v(" This remedy is an equitable one that has its origins in Roman law, and which was subsequently accepted in Roman-Dutch law. Two landmark cases that dealt with this principle are that of Gowan v Bowern17 and MacDuff & Co Ltd v Johannesburg Consolidated Investment Co Ltd18. In the MacDuff case Innes CJ succinctly stated the position as follows: ")]),_c('p',[_vm._v(" “[B]y our law a condition is deemed to have been fulfilled as against a person who would, subject to its fulfilment, be bound by an obligation, and who has designedly prevented its fulfilment, unless the nature of the contract or the circumstances show an absence of dolus on his part”. ")]),_c('p',[_vm._v(" Dolus in this context does not bear its usual meaning of deliberate wrongdoing or fraudulent intent but a more specific meaning, namely, the deliberate intention of preventing the fulfilment of the condition in order to escape the obligation subject to it. ")]),_c('p',[_vm._v(" In Gowan v Bowern Wessels JA stated that: ")]),_c('p',[_vm._v(" “The Court must hld that if a contract is made subject to a casual condition then if the person in whose interest it is that it should not be fulfilled deliberately does some act by which he hinders the accomplishment of the condition, he is liable as if the condition had been fulfilled. But a party cannot be said to frustrate a condition unless he actively does something by which he hinders its performance. There must be an intention on his part to prevent his obligation coming into force”. ")]),_c('p',[_vm._v(" In order to successfully invoke the doctrine of fictional fulfilment, a plaintiff must prove the following (per Holmes JA in Scott and Another v Poupard and Another): ")]),_c('div',[_c('ol',[_c('li',[_vm._v(" non–fulfilment of the condition")]),_c('li',[_vm._v("the defendant’s breach of his duty with an intention to frustrate the fulfilment, and")]),_c('li',[_vm._v("a causal link between (a) and (b)")])])])])]),_c('li',{staticClass:"h5"},[_vm._v("Resolutive Condition")]),_c('p',[_vm._v(" A resolutive condition is a contractual term which renders the continued existence of the contract dependant on the occurrence (or non-occurrence) of a specified uncertain future event.19 A contract subject to a resolutive condition is immediately binding with all rights and obligations coming into existence and becoming enforceable from the time of the agreement. There is no suspension of rights and obligations. Unlike a suspensive condition, if a resolutive condition is fulfilled, it would have the effect of terminating the contract with retrospect between the parties and the operation of the rights and obligations between the parties would immediately cease to exist. The parties would then need to be restored to their pre-contractual state, which is often complicated as this may for example require the unbundling of a complicated transaction. In this instance it may be better suited, to avoid such unbundling, to make use of a suspensive condition as opposed to a resolutive condition. ")]),_c('p',[_vm._v(" An example of a resolutive condition would be as follows: ")]),_c('p',[_vm._v(" “In the event Mrs Smith does not provide proof that she has resigned as a director of ABC Company by 15 March 2019, the contract will be deemed to be cancelled and be of no force or effect” ")])]),_c('ol',[_c('li',{staticClass:"h5"},[_vm._v("Warranty (Guarantee)")]),_c('p',[_c('ol',[_c('li',{staticClass:"h6"},[_vm._v("What is a Warranty?")]),_c('p',[_vm._v(" The concept of a warranty bears a variety of meanings. According to Sharrock, warranty or ‘guarantee’ in the strict sense is a contractual undertaking that a certain state of affairs exists or that a certain act will be performed or a state of affairs will exist in the future20. Hutchinson defines a warranty as a term inserted in a contract in favour of one party to extend the liability of the other party beyond what it would normally be21.Havenga defines a warranty as a contractual term whereby a contracting party accepts absolute responsibility for proper performance relating to the absence of defects in the warrantor’s product or service, or to the possibility that that the warrantor is able to render the performance, or to the quality or standard of the warrantor’s product or service, or to the quantity of performance etc. ")]),_c('p',[_vm._v(" A party who is concerned that the other party may escape liability on the ground that no obligation was created due to an initial objective impossibility of performance could insist that the other party should warranty performance. If the warranty is agreed to, the obligation is ")]),_c('p',[_vm._v(" Is created, even though performance is impossible. The party in breach of the warranty would then be liable to pay damages22. ")]),_c('div',{staticClass:"example z-depth-2 bg2 p-3 m-4"},[_c('h6',{staticClass:"text-left class h6"},[_vm._v("Example")]),_c('em',[_c('p',[_vm._v(" The seller of a motor vehicle may undertake that that the vehicle has travelled no more than a stated number of kilometres or that it will achieve a particular level of fuel consumption if driven under certain conditions. If the vehicle has done more kilometres or uses more fuel than promised, the warrantor may be held liable in damages for breach of contract. ")])])]),_c('li',{staticClass:"h6"},[_vm._v("Types of Warranty")]),_c('p',[_vm._v(" A warranty may be express or implied23. If it is clear from the circumstances that the mutual intention of the parties was that a statement should be a term of the contractor, the term will be an implied warranty. In certain cases warranties are implied by law (such as the implied warranty against eviction, and the implied warranty against latent defects in every contract of purchase and sale). ")]),_c('li',{staticClass:"h6"},[_vm._v("Common - Law Warranty")]),_c('p',[_vm._v(" In the local case of Lungisani Vilakati v Purell (Pty) Ltd24, it was stated that the duty of establishing the existence and operation of an implied term in a contract falls within the onus of proof which is ordinarily on a balance of probabilities. In discharging this onus the courts have come to recognise the so called hypothetical officious bystander fiction as an aid for assigning the common intention of the parties. In the classic oft-quoted dictum of De Villiers J, in Simon v DCU Holdings (Pty) Ltd and others where he said: ")]),_c('div',[_c('p',[_vm._v(" In the local case of Lungisani Vilakati v Purell (Pty) Ltd24, it was stated that the duty of establishing the existence and operation of an implied term in a contract falls within the onus of proof which is ordinarily on a balance of probabilities. In discharging this onus the courts have come to recognise the so called hypothetical officious bystander fiction as an aid for assigning the common intention of the parties. In the classic oft-quoted dictum of De Villiers J, in Simon v DCU Holdings (Pty) Ltd and others where he said: ")])]),_c('p',[_vm._v(" In Alfred McAlpine & Son (Pty) Ltd v Transvaal Provincial Administration25 this is how the court explained and articulated the concept of an implied term of contract: ")]),_c('div',[_c('p',[_vm._v(" “In legal parlance the expression \"implied term”.,, is used to describe an unexpressed provision of the contract which the law imports therein, generally as a matter of cause, without reference to the actual intention of the parties. The intention of the parties is not totally ignored. Such a term is not normally implied if it is in conflict with the express provision of the contract. On the other hand it does not originate in the contractual consensus: it is imposed by the law from without. Indeed, terms are often implied by the law in cases where it is by no means clear that the parties would have agree to incorporate them in their contracts. Such implied terms may derive from the common law, trade usage or custom, or from statute. In a sense “implied term” is, in this context, a misnomer in that in content it simply represents a legal duty (giving rise to a correlative right) imposed by law, unless excluded by the parties, in the case of certain classes of contracts, it is naturalium of the contract in question.” ")])])])])])]),_c('p')])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('h1',{staticClass:"text-center chapter-topic secondary-heading text-primary"},[_c('strong',[_c('i',{staticClass:"fas fa-book-reader"}),_vm._v(" Summary")])]),_c('div',[_c('p',[_vm._v(" This brings us to the end of Unit 1 where we unpacked conditions and warranties. We were introduced to these new concepts which are very familiar in the law of contract. In this unit we also covered the different types of conditions as well as the different types of warranties. In the discussion on warranties we highlighted the common law warranty which is similar to an implied term of a contract. We will now proceed to Unit 2 to discuss variations and waiver. Enjoy! ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"Chapter",attrs:{"id":"activity1"}},[_c('h1',{staticClass:"text-center chapter-topic secondary-heading text-primary",attrs:{"id":"activity1"}},[_c('strong',[_c('i',{staticClass:"fas fa-tools"}),_vm._v("Activity 1")])]),_c('p',[_vm._v(" With reference to your LAW203 and the current unit, distinguish between terms and conditions of a contract. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"Chapter"},[_c('h1',{staticClass:"text-center chapter-topic  secondary-heading text-primary"},[_c('strong',[_c('i',{staticClass:"fas fa-tools"}),_vm._v(" Activity 2")])]),_c('p',[_vm._v(" A bought a car from B at a price of E15,000.00. A was to pay the total amount of E15,000.00 in cash before gaining possession of the car. Further to that, B was to deliver the car to A on or before the lapse of three days on receipt of the money paid in full. A paid the required amount but B delivered the vehicle 10 days later. On delivery, a note was given to A stating that the vehicle would be under warrant for 6 months on delivery. The warrant would only cover mechanical problems which should not be caused by conduct of A (buyer). Two months later, the vehicle developed mechanical problems and A took it back to B and demanded refund of the cash paid. B refuses to entertain A. ")]),_c('p',[_vm._v("What are the legal issues? ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"Chapter"},[_c('h1',{staticClass:"text-center chapter-topic secondary-heading text-primary"},[_c('strong',[_c('i',{staticClass:"fas fa-book-reader"}),_vm._v("Self-Assessment")])]),_c('ol',[_c('li',[_vm._v(" Taylor sees an ad for a 32-inch TV for $100 and goes to the store to buy it. He pays the $100 and takes the TV home, but upon opening the box he discovers the TV is only 27 inches. What is this an example of? ")]),_c('li',[_vm._v(" Landon buys a new particular type of lawnmower after the salesperson suggested it due to the type of grass in Landon's lawn. However, when Landon uses the mower it doesn't cut the grass. Instead, it just pushes the grass blades to the side. What is this an example of? ")]),_c('li',[_vm._v(" A told B, a car dealer that he wanted to purchase a car ' suitable for touring purposes'. B suggested that a 'Bugatti' car would be fit for the purpose. Relying upon this statement, A bought a ' Bugatti' car which turned out to be unfit for touring purposes, advise A. ")])])])}]

